/**
 * Direction enum regroup the 8 directions that an avatar can handle on Habbo.
 *
 * @enum
 * @memberof Scuti
 */
export enum Direction {
    NORTH,        /** Direction 0 */
    NORTH_EAST,   /** Direction 1 */
    EAST,         /** Direction 2 */
    SOUTH_EAST,   /** Direction 3 */
    SOUTH,        /** Direction 4 */
    SOUTH_WEST,   /** Direction 5 */
    WEST,         /** Direction 6 */
    NORTH_WEST    /** Direction 7 */
}
